.nav {
  background-color: #ffffff;
  color: #424242;
  height: 6em;
  box-shadow: 0 2px 2px #bdbdbd;
  width: 100%;
  position: relative; }
  .nav__inner {
    max-width: 60em;
    margin: 0 auto;
    height: 100%; }
  .nav__homepage {
    text-decoration: none;
    color: #424242;
    height: 100%;
    display: block;
    float: left; }
  .nav__icon {
    max-height: 100%;
    padding: 1.5em .75em 1.5em .5em;
    float: left;
    display: block; }
    @media all and (min-width: 50em) {
      .nav__icon {
        padding: 1em; } }
  .nav__title {
    line-height: 5em;
    display: block;
    float: left;
    font-size: 1.2em; }
    @media all and (min-width: 50em) {
      .nav__title {
        font-size: 1.5em;
        line-height: 4em; } }
  .nav__menu-toggle {
    display: block;
    float: right;
    font-size: 1.2em;
    height: 100%;
    line-height: 5em;
    padding-right: .5em;
    cursor: pointer;
    user-select: none;
    text-transform: uppercase; }
    @media all and (min-width: 50em) {
      .nav__menu-toggle {
        display: none; } }
  .nav__menu-checkbox {
    display: none; }
  .nav__menu {
    transform: scaleY(0);
    transform-origin: top;
    position: absolute;
    top: 6em;
    padding: 0;
    width: 100%;
    margin: 0;
    float: right;
    z-index: 10;
    box-shadow: 0 2px 2px 0 #bdbdbd;
    transition: transform 0.3s cubic-bezier(1, 0.07, 0.13, 0.9); }
    .nav__menu-checkbox:checked + .nav__menu {
      transform: scaleY(1); }
      .nav__menu-checkbox:checked + .nav__menu .nav__link {
        opacity: 1; }
    @media all and (min-width: 50em) {
      .nav__menu {
        transform: scaleY(1);
        top: 0;
        position: relative;
        width: auto;
        box-shadow: none;
        height: 100%; } }
  .nav__item {
    display: block;
    width: 100%;
    float: left;
    background-color: #fff3e0; }
    .nav__item:nth-child(2) {
      background-color: #ffe0b2; }
    .nav__item:nth-child(3) {
      background-color: #ffcc80; }
    .nav__item:nth-child(4) {
      background-color: #ffb74d; }
    @media all and (min-width: 50em) {
      .nav__item {
        width: auto;
        float: left;
        height: 100%;
        background-color: white !important; } }
  .nav__link {
    color: #424242;
    text-decoration: none;
    display: block;
    font-size: 1.2em;
    padding: 1em 1.5em;
    opacity: 0;
    transition: opacity 0.3s cubic-bezier(1, 0.07, 0.13, 0.9); }
    @media all and (min-width: 50em) {
      .nav__link {
        display: inline-block;
        opacity: 1;
        padding: 2em 1.5em; } }

.masthead {
  min-height: 400px;
  background-size: cover;
  width: 100%;
  position: relative; }
  .masthead__inner {
    max-width: 60em;
    margin: 0 auto;
    position: relative;
    height: 100%;
    min-height: 400px; }
  .masthead:after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #ff9800;
    opacity: 0.5;
    content: ''; }
  .masthead__title {
    position: absolute;
    bottom: 10px;
    left: 10px;
    z-index: 2;
    color: #0d47a1; }

/* --- UTILITY --- */
.u-visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden; }

body:hover .u-visually-hidden a,
body:hover .u-visually-hidden input,
body:hover .u-visually-hidden button {
  display: none !important; }

/**
 * For modern browsers
 * 1. The space content is one way to avoid an Opera bug when the
 *    contenteditable attribute is included anywhere else in the document.
 *    Otherwise it causes space to appear at the top and bottom of elements
 *    that are clearfixed.
