.u-visually-hidden {
    position: absolute !important;
    clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
    clip: rect(1px, 1px, 1px, 1px);
    padding:0 !important;
    border:0 !important;
    height: 1px !important;
    width: 1px !important;
    overflow: hidden;
}

body:hover .u-visually-hidden a,
body:hover .u-visually-hidden input,
body:hover .u-visually-hidden button { 
    display: none !important;
}
